<template>
  <div style="background:white;height:100%;">
    <div class="SettingHeader">
      <mu-appbar style="width: 100%;" :color="$store.state.color">
        <div slot="left" color="#3051f1">
          <a @click="()=>{
              $router.go(-1)
            }">
            <mu-button icon slot="left" class="icon" size="20">
              <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
            </mu-button>
          </a>
        </div>
        <div style="width:100%;text-align:center;">
          <span style="font-size:18px;color:white;">车辆管理</span>
        </div>
        <div slot="right" class="icon" size="20">
          <a href="#/addCar">
            <mu-button icon slot="left" class="icon" size="20">
              <mu-icon value="add" size="24" color="white"></mu-icon>
            </mu-button>
          </a>
        </div>
      </mu-appbar>
    </div>

    <div class="carHeader flex">
      <div>录入时间</div>
      <div>车辆编号</div>
      <div>车牌号</div>
      <div>驾驶人</div>
    </div>
    <div class="settingContent">
      <div class="carContent flex" v-for="(item,index) in itemList" :key="index">
        <div>{{item.gmtCreate}}</div>
        <div>{{item.name}}</div>
        <div>{{item.number}}</div>
        <div>暂无</div>
      </div>
    </div>
  </div>
</template>
<script>
import Warning from "../../../static/js/warning.js";
import request from "../../../static/js/request.js";
import Appbace from "../../../static/ajax.js"
export default {
  name: "aboutMen",
  data() {
    return {
      token: "",
      itemList: [],
      token: ""
    };
  },
  methods: {
    async initData() {
      var url = "/job/vehicle";
      var res = await Appbace.request(url, "GET", {
        
      }, this.token);
      this.itemList = res.data.data;
      for (let index = 0; index < this.itemList.length; index++) {
        var element = this.itemList[index].gmtCreate;
        this.itemList[index].gmtCreate = element.slice(0,10);
        
      }
      console.log(res);
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.initData();
  }
};
</script>
<style scoped>
.settingContent {
  width: 100%;
  text-align: center;
  margin-top: 96px;
}
.flex {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-evenly;
  height: 40px;
  align-items: center;
}
.flex div {
  width: 25%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.carHeader {
  background: white;
  border-bottom: 1px solid rgb(202, 201, 201);
  box-shadow: 0px 2px 0px rgb(245, 250, 253);
  font-weight: 600;
  position: fixed;
  top: 56px;
  /* margin-bottom: 60px; */
}
.SettingHeader {
  position: fixed;
  top: 0;
  width: 100%;
}
.carContent {
  border-bottom: rgb(236, 235, 235) 1px solid;
}
</style>



